<script>
import { mapState, mapActions } from 'vuex'
import { priceMixin } from '@/mixins/formatter.js';
import { loaderMixin } from '@/mixins/dateLoaderMixins.js'
import IpTransfer from '@/components/modals/Outsource/Transfer.vue'
import DeleteModal from '@/components/modals/DeleteModal.vue'

export default {
    mixins: [priceMixin, loaderMixin],
    components: {
        IpTransfer,
        DeleteModal
    },
    data() {
        return {
            page: 1,
            pageCount: 0,
            editModal: false,
            deleteModal: false,
            ipTransferDialog: false,
            itemData: {},
            headers: [
                {text: 'Berilgan ip kodi', value: 'ip_code'},
                {text: 'Berilgan ip nomi', value: 'ip_title'},
                {text: 'Ip massasi', value: 'ip_mass'},
                {text: 'Ipni qaytarish', value: 'ip_return'},
                {text: 'Harakatlar', value: 'actions', align: 'center'},
            ],
            items: []
        }
    },
    async mounted(){
        await this.refresh()
    },
    watch:{
        '$route.params.id': {
            handler: async function(value) {
                console.log(value)
                await this.refresh()
            },
            deep: true,
            immediate: true
        },
        '$store.state.main.outsource_warehouse': {
            handler: async function(value) {
                if(value){
                    await this.refresh()
                    this.$store.commit('main/SET_UPDATE_OUTSOURCE_WAREHOUSE', false)
                }
            },
            deep: true,
            immediate: true
        },
        page: async function (newVal, oldVal) {
            if (newVal) {
                await this.refresh()
            }
        },
        // update: async function (newVal, oldVal) {
        //     if (newVal) {
        //         await  this.refresh()
        //     }
        // },
    },
    computed: {
        ...mapState({
            factory_threads: state => state.outsource.factory_threads
        }),
    },
    methods:{
        ...mapActions({
            factoryThreads: 'outsource/factoryThreads'
        }),
        async refresh(){
            this.loading = true
            await this.factoryThreads({ page: this.page, product_case: 'raw', factory: this.$route.params.id })
            this.page = this.factory_threads.current_page;
            this.pageCount = Math.ceil(this.factory_threads.total / 10);
            
            this.items = this.factory_threads.data
            this.items.forEach((e) => {
                e.ip_code = e.product.code;
                e.ip_title = e.product.name;
                e.ip_mass = e.amount;
                e.ip_return = 'Otkazish';
            })
            this.loading = false
        },
        OpenTransferDialog(item){
            this.itemData = item.product
            this.ipTransferDialog = true
        },
        closeEditModal(){
            this.editModal = false
        }
        
    }
}
</script>

