<script>
import { mapState, mapActions } from 'vuex'
import { priceMixin } from '@/mixins/formatter.js';
import { loaderMixin } from '@/mixins/dateLoaderMixins.js'
import ProductTook from '@/components/modals/Outsource/ProductTook.vue'
import DeleteModal from '@/components/modals/DeleteModal.vue'

export default {
    mixins: [priceMixin, loaderMixin],
    components: {
        ProductTook,
        DeleteModal
    },
    data() {
        return {
            tookDialog: false,
            editModal: false,
            deleteModal: false,
            page: 1,
            pageCount: 0,
            headers: [
                {text: 'Kodi', value: 'code'},
                // {text: 'Turi', value: 'type'},
                {text: 'Nomi', value: 'name'},
                {text: 'Buyurtma', value: 'amount'},
                {text: 'Qoldi', value: 'left'},
                {text: 'KG narxi', value: 'price'},
                {text: 'Summasi', value: 'total_price'},
                {text: 'Sana', value: 'date'},
                {text: 'Harakatlar', value: 'actions', align: 'center'},
            ],
            items: [],
            item: {}
        }
    },
    async mounted(){
        await this.refresh()
    },
    watch:{
        '$route.params.id': {
            handler: async function(value) {
                await this.refresh()
            },
            deep: true,
            immediate: true
        },
        '$store.state.main.outsource_orders': {
            handler: async function(value) {
                if(value){
                    await this.refresh()
                    this.$store.commit('main/SET_UPDATE_OUTSOURCE_ORDERS', false)
                }
            },
            deep: true,
            immediate: true
        },
        page: async function (newVal, oldVal) {
            if (newVal) {
                await this.refresh()
            }
        },
        // update: async function (newVal, oldVal) {
        //     if (newVal) {
        //         await  this.refresh()
        //     }
        // },
    },
    computed: {
        ...mapState({
            orders: state => state.outsource.orders,
            item: state => state.outsource.item
        })
    },
    methods: {
        ...mapActions({
            fetchOrders: 'outsource/fetchOrders'
        }),
        async refresh(){
            this.loading = true
            await this.fetchOrders({type: 'collar', factory: this.$route.params.id, status: 'in_process'})
            this.page = this.orders.current_page;
            this.pageCount = Math.ceil(this.orders.total / 10);
            
            this.items = this.orders.data;
            this.items.forEach((elem) => {
                let left_amount = 0;
                elem.order_incomes.forEach((a) => {
                    left_amount = left_amount + a.amount
                })
                elem.name = elem.product.name;
                elem.date = elem.created_at;
                elem.left = elem.amount - left_amount
                elem.date = this.$dayjs(elem.created_at).format('DD-MM-YYYY') 
            })
            this.loading = false
        },
        async openProductTook(item){
            this.tookDialog = true
            this.item = await {product: ''}
            this.item = await item
        },
        closeEditModal(){
            this.editModal = false
        },
    }
}
</script>

